.input-with-label {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.input-with-label__cont {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
.input-with-label__row {
  display: flex;
  justify-content: space-between;
}
.input-with-label__row_2 .input-with-label {
  width: calc((100% / 2) - 10px);
}
.input-with-label__row_3 .input-with-label {
  width: calc((100% / 3) - 10px);
}
.input-cont {
  position: relative;
}
.input-cont input {
  width: 100%;
}
.input-cont .input__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 10px;
}
label {
  font-size: 0.92857143rem;
  color: #243970;
  font-weight: 600;
}
input:not([type='submit']),
textarea {
  font-size: 1rem;
  padding: 9px;
  border-radius: 5px;
  border: 1px solid #dce4e8;
  background: #ffffff;
  color: #243970;
}
textarea {
  resize: none;
  height: 80px;
}
textarea:focus {
  outline: none;
}
@media (max-width: 500px) {
  .input-with-label__row_2 {
    flex-direction: column;
  }
